import React from "react"
import { graphql } from "gatsby"

import Calendar from "../components/calendar"
import Layout from "../components/layout"
import SEO from "../components/seo"
import TwitterShareButton from "../components/twitter_share_button"
import ogImage from '../images/yoppinews.png'
import thumbnail from '../images/favicon.jpg'

const IndexPage = ({ props, data }) => (
  <Layout>
    <SEO title="yoppinews" keywords={[`yoppinews`, `青山吉能`]} image={props.location.pathname + ogImage} thumbnail={props.location.pathname + thumbnail} />
    <p>
      <TwitterShareButton/>
    </p>
    <p>
      <a href="https://twitter.com/yoppinews" target="blank">{data.site.siteMetadata.title}</a> は
      81 プロデュース所属の声優・青山吉能さん関連の情報を集約する非公式の twitter アカウントです
    </p>
    <Calendar/>
    <ul>
      <li>情報の不足・誤りについては <a href="https://twitter.com/yoppinews">Twitter DM</a> にてご連絡いただけますと幸いです</li>
      <li>公式な情報ソースのないものについては一部記載を見送っているものがある場合があります</li>
    </ul>
    <p>
      <a className="twitter-timeline" href="https://twitter.com/yoppinews?ref_src=twsrc%5Etfw">Tweets by yoppinews</a>
    </p>
  </Layout>
)

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`

export default IndexPage
